import enigma from "enigma.js"
import schema from "enigma.js/schemas/12.936.0.json"
import {v4 as uuid} from 'uuid'

import {appConfig as defaultAppConfig} from "../config/app_config.js"
import {getKpiConfig} from "../config/kpiConfig.js"
let defaultKpiConfig = getKpiConfig(defaultAppConfig);

// import {Observable} from "rxjs"
// https://rxjs.dev/guide/observable To be implemented later.

// Define Generic object to get fields or aggr values based on expression submitted in qFieldDefs
let listObjectProps = {
    qInfo:{
        qType:'session-gen-object'
    },
    qListObjectDef: {
        qStatename: '$',
        qShowAlternatives: true,
        qDef: {
            qFieldDefs: ['Account Class']
        },
        qInitialDataFetch: [{
            "qHeight": 10000,   //the number of rows to pull from the starting position
            "qWidth": 1,        //the number of columns to pull from the starting position
            "qLeft": 0,         //the column position to start at. 0 corresponds with the first column
            "qTop": 0,          //the row position to start at. 0 corresponds with the first row
        }],
    }
}

// // Define Hypercube object model to get "table" data back from Qix Engine
let hyperCubeProps = {
    qInfo:{
        qType:'session-list-object'
    },
    qHyperCubeDef: {
        qStatename: '$',
        qShowAlternatives: true,
        qDimensions:[
            {
                qDef: {
                    qFieldDefs: ['Account Class'],
                    qSortCriterias: [
                        {
                            qSortByNumeric: 1,
                            //qSortByExpression: 1,
                            //qExpression:""
                        }
                    ]
                },
            }
            //https://help.qlik.com/en-US/sense-developer/June2017/Subsystems/EngineAPI/Content/Structs/NxInlineDimensionDef.htm
        ],
        qMeasures:[
            {
                qDef:{
                    qDef: "=sum([Avg Listing Price] * [Total Listing Count])/sum([Total Listing Count])"
                }
            }
            //https://help.qlik.com/en-US/sense-developer/June2017/Subsystems/EngineAPI/Content/Structs/NxMeasure.htm
        ],
        qInitialDataFetch: [{
            "qHeight": 1000,   //the number of rows to pull from the starting position
            "qWidth": 2,        //the number of columns to pull from the starting position
            "qLeft": 0,         //the column position to start at. 0 corresponds with the first column
            "qTop": 0,          //the row position to start at. 0 corresponds with the first row
        }],
    }
}
//https://help.qlik.com/en-US/sense-developer/June2017/Subsystems/EngineAPI/Content/Structs/HyperCube.htm

/**
 * KPI Controller Class Component
 * @param {Object} envConfig - Environment Configuration File
 * @param {Object} kpiConfig - kPI Configuration File
 * 
 */
class KPIController {
    constructor(envConfig, kpiConfig = defaultKpiConfig, appConfig = defaultAppConfig){
        // Config Parameter
        this.env = envConfig,
        this.kpiConfig = kpiConfig
        this.appConfig = appConfig
        // Cache for the KPI Data to prevent duplicate calls to Qlik
        this.kpiCache = {}
        this.identity = uuid().split("-")[4]
    }

    /**
     * classMethod (Public) - getKPI 
     *  Will return values from Qlik according to the KPI Config
     * 
     * @param {string} kpiID - The reference ID corresponding to the id in the kpiConfig
     */
    async getKPI(kpiID){
        // Initial Generic Object
        let obj = {
            qInfo:{
                qType:'session-gen-object'
            },
        }

        // Get KPI Config
        let kpi = this.kpiConfig.find((kpi) => kpi.id === kpiID)

        // 1. Find KPI in Config and build single Application Query
        obj[kpiID]  = this._getKPIObject(kpi)
                
        //Get App ID from Environment Config
        let appid = this.env.apps.find((a)=> a.app === kpi.app)["id"]
        
        // 2. Use KPI Config to call Qlik
        let session = await this._getEnigmaSession(appid)
        let global = await session.open()
        let app = await global.openDoc(appid)

        // Set app selections
        await this._setAppSelections(app, kpi.app)
        
        // Execute KPI SessionObject
        let sessionObj = await app.createSessionObject(obj)
        let layout = await sessionObj.getLayout()
        let formatedLayout = await this._xfrmLayout(layout[kpiID],kpiID)
        // Close Connection
        await session.close()

        //If Custom Transform Exists then execute transform function
        if(kpi.transform) {
            formatedLayout = kpi.transform(formatedLayout)
        }

        // 3. Return Promise with value.
        let output = {}
        output[kpiID] = formatedLayout
        return output

        // Not a feature to implement at this time....
        // Listen for layout changes and repull layout
        // sessionObj.on("changed", ()=>{
        //     layout = await sessionObj.getLayout()
        // })
    }

    /**
     * classMethod (Public) - getAsyncKPIs
     *  Will return an array of Promise Values for Data returned from Qlik according to the KPI Configs provided
     * 
     * @param [string] kpis - Array of string values representing KPI Ids from the kpiConfig file
     */
    async getAsyncKPIs(kpis=[]){
        results = []
        kpis.map((kpi)=>{
            results.push(getKPI(kpi))
        })

        return results
    }

    /**
     * classMethod (Public) - getKPIs
     *  Will return an object for the Data returned from Qlik according to the KPI Configs provided
     * 
     * @param [string] kpis - Array of string values representing KPI Ids from the kpiConfig file
     *        [string] mode - ID (IDs list), KPI (KPIs list), APP (APPs list)
     *        If both parameters are omitted, all KPIs are returned
     */           
    async getKPIs(kpis=[], mode = 'ID'){
        //let output = {}
        let output = []
        let kpiDetails = this.kpiConfig.filter((k)=> (kpis.includes(k.id) && mode == 'ID') || 
                                                     (kpis.includes(k.kpi) && mode == 'KPI') || 
                                                     (kpis.includes(k.app) && mode == 'APP') || 
                                                      kpis.length == 0);

        // Get unique apps
        let apps = kpiDetails.map((d)=> d.app).filter((value,index,self)=>{
            return self.indexOf(value) === index;
        })

        let p = [];
        for (let index = 0; index < apps.length; index++) {
            const app = apps[index];

            let appKpis = kpiDetails.filter((d)=> d.app === app)
            //Get App ID from Environment Config
            let appid = this.env.apps.find((a)=> a.app === app)["id"]
            
            p[index] = this._getKPIsOutput(app, appid, appKpis).then((formatedLayout)=>{
                            /*output = {
                                ...output,
                                ...formatedLayout
                            }*/
                            //console.log(formatedLayout);
                            //output.concat(formatedLayout); 
                            output = [
                                ...output,
                                ...formatedLayout
                            ];
                        });
        }

        await Promise.all(p); 

        // Test Hypercube Parse
        // let data = await this._getHypercubeData(layout.series.qHyperCube)
        // console.log("data", data)

        // Return Promise with value.
        return output        
    }    

    async _getKPIsOutput(app, appid, appKpis) { 
        // 2. Use KPI Config to call Qlik
        let session = await this._getEnigmaSession(appid),
            global = await session.open(),
            qapp = await global.openDoc(appid),
            genObj = {
                qInfo:{
                    qType:'session-gen-object'
                },
            };

        // Set app selections
        await this._setAppSelections(qapp, app)

        // Map through KPIs and create large generic Object
        appKpis.map((kpi)=>{
            // Get Object Definition
            genObj[kpi.id] = this._getKPIObject(kpi)
        })

        // Execute KPI SessionObject
        let sessionObj = await qapp.createSessionObject(genObj)
        let layout = await sessionObj.getLayout()
        //let formatedLayout = {}
        let formatedLayout = []
        for (let index = 0; index < appKpis.length; index++) {
            const kpi = appKpis[index];
            //formatedLayout[kpi.id] = await this._xfrmLayout(layout[kpi.id], kpi.id)
            let kpi_layout = await this._xfrmLayout(layout[kpi.id], kpi.id)

            //If Custom Transform Exists then execute transform function
            if(kpi.transform) { 
                //formatedLayout[kpi.id] = kpi.transform(formatedLayout[kpi.id])
                formatedLayout.push(kpi.transform(kpi_layout));
            }
        }

        // Close Connection
        session.close();
        return formatedLayout;
    }

    getAvailableFilters(appName){
        let appConfig = this.appConfig[appName]
        let filters = []
        for (let index = 0; index < appConfig.variables.length; index++) {
            const appVar = appConfig.variables[index];
            filters.push(appVar.id)            
        }
        for (let index = 0; index < appConfig.selections.length; index++) {
            const appVar = appConfig.selections[index];
            filters.push(appVar.id)            
        }

        return filters
    }

    setFilter(filterName, value, appName){
        let appConfig = this.appConfig[appName]

        appConfig.selections = appConfig.selections.map((s,idx)=>{
            if(s.id === filterName){
               s.value = value
            }
            return s
        })

        appConfig.variables = appConfig.variables.map((v,idx)=>{
            if(v.id === filterName){
                v.value = value
            }
            return v
        })

        return
    }

    setFieldSelection(app, fieldID, val) {
        this.appConfig[app].selections.map(function (elem) {
            if(elem.id === fieldID)
                elem.value = val;
        });
    }

    async getKPIsRon(kpis=[], mode = 'ID'){        
        let output = {}
        let kpiDetails = this.kpiConfig.filter((k)=> (kpis.includes(k.id) && mode == 'ID') || 
                                                     (kpis.includes(k.kpi) && mode == 'KPI') ||
                                                     (kpis.includes(k.app) && mode == 'APP') || 
                                                      kpis.length == 0);

        // Get unique apps
        let apps = kpiDetails.map((d)=> d.app).filter((value,index,self)=>{
            return self.indexOf(value) === index;
        })

        for (let index = 0; index < apps.length; index++) {
            const app = apps[index];

            let appKpis = kpiDetails.filter((d)=> d.app === app)
            let genObj = {
                qInfo:{
                    qType:'session-gen-object'
                },
            }
            //Get App ID from Environment Config
            let appid = this.env.apps.find((a)=> a.app === app)["id"]
            
            // 2. Use KPI Config to call Qlik
            let session = await this._getEnigmaSession(appid)
            let global = await session.open()
            let qapp = await global.openDoc(appid)
    
            // Set app selections
            await this._setAppSelections(qapp, app)

            // Map through KPIs and create large generic Object
            appKpis.map((kpi)=>{

                // Get Object Definition
                genObj[kpi.id] = this._getKPIObject(kpi)

            })
    
            // Execute KPI SessionObject
            let sessionObj = await qapp.createSessionObject(genObj)
            let layout = await sessionObj.getLayout()
            let formatedLayout = {}
            for (let index = 0; index < appKpis.length; index++) {
                const kpi = appKpis[index];
                formatedLayout[kpi.id] = await this._xfrmLayout(layout[kpi.id], kpi.id)

                //If Custom Transform Exists then execute transform function
                if(kpi.transform) {
                    formatedLayout[kpi.id] = kpi.transform(formatedLayout[kpi.id])
                }
            }


            output = {
                ...output,
                ...formatedLayout
            }
            // Close Connection
            await session.close()
        }

        // Test Hypercube Parse
        // let data = await this._getHypercubeData(layout.series.qHyperCube)
        // console.log("data", data)

        // 3. Return Promise with value.
        return output
        
    }

    /**
     * classMethod (Private) - _getEnigmaSession
     *  Will return an enigma session object for connecting via websockets to Qlik.
     * 
     * @param {string} appid - Qlik Application ID (GUID)
     */    
    async _getEnigmaSession(appid){
        var config = {
            schema,
            url: `wss://${this.env.host}/${this.env.proxy}/app/${appid || 'engineData'}`,
            createSocket: url => new WebSocket(`wss://${this.env.host}/${this.env.proxy}/app/${appid || 'engineData'}/identity/${this.identity}`)
        }
        // log.info("Starting Engine Session.");
        // log.trace({"EngineConfig": config}, "Engine Config");
        var session = await enigma.create(config);
        // session.on('traffic:sent', data => log.trace({type:"sent", "EngineData":data},"Engine Traffic: Sent"));
        // session.on('traffic:received', data => log.trace({type:"received", "EngineData":data},'Engine Traffic Received:'));
    
        return session;

    }

    _getHypercubeData(cube){
        try {
            var dims = cube.qDimensionInfo.map((dim,idx) => {
                return {
                    id: idx,
                    name: dim.qFallbackTitle,
                }
            });

            var measures = cube.qMeasureInfo.map((m,idx) => {
                return {
                    id: dims.length + idx,
                    name: m.qFallbackTitle,
                };
            });

            var columns = dims.concat(measures);

            var qpages = cube.qDataPages;

            var data = [];
            //Loop through the Pages
            for (let page = 0; page < qpages.length; page++) {
                const qmatrix = qpages[page].qMatrix;

                //Loop through the data elements
                for (let index = 0; index < qmatrix.length; index++) {                    
                    let row = qmatrix[index]; 
                    row = row.map((r)=>{
                        return {"text":r.qText, "value": r.qNum}
                    })
                    data.push(row)
                }
                
            }
            return data            
        } catch (error) {
            console.error(error.message);
        }            
    }

    _getListData(listObject){
        try {
            var title = listObject.qDimensionInfo.qFallbackTitle
            var qpages = listObject.qDataPages;

            var list = [];
            //Loop through the Pages
            for (let page = 0; page < qpages.length; page++) {
                const qmatrix = qpages[page].qMatrix;

                //Loop through the data elements
                for (let index = 0; index < qmatrix.length; index++) {                    
                    const row = qmatrix[index][0];                                        
                    list.push({text:row.qText, value:row.qNum})
                }
                
            }
            return {"title": title, "list": list};            
        } catch (error) {
            console.error(error.message);
        }            
    }
    
    _getKPIObject(kpi) {
        let obj = {}
        // Get Single Value 
        kpi?.singleValueData?.map((val) => {
            obj[val.id] = { text: { qStringExpression: "" }, value: { qValueExpression: "" } }
            obj[val.id].text.qStringExpression = val.expression
            obj[val.id].value.qValueExpression = val.expression
        })
        // Get List Expressions
        kpi?.listData?.map((list) => {
            obj[list.id] = { qListObjectDef: JSON.parse(JSON.stringify(listObjectProps.qListObjectDef)) } //deep copy the listObjectDef
            obj[list.id].qListObjectDef.qDef.qFieldDefs[0] = list.expression
        })
        // Get Cube Expressions
        kpi?.cubeData?.map((cube) => {
            obj[cube.id] = { qHyperCubeDef: JSON.parse(JSON.stringify(hyperCubeProps.qHyperCubeDef)) } //deep copy the hyperCubeDef
            obj[cube.id].qHyperCubeDef.qDimensions[0].qDef.qFieldDefs[0] = cube.dimension
            obj[cube.id].qHyperCubeDef.qMeasures[0].qDef.qDef = cube.measure

            cube?.singleValueData?.map((val) => {
                let id = cube.id+'_'+val.id;
                obj[id] = { text: { qStringExpression: "" }, value: { qValueExpression: "" } }
                obj[id].text.qStringExpression = val.expression
                obj[id].value.qValueExpression = val.expression
            })
        })
        // Get Def Expressions
        kpi?.defData?.map((cube) => {
            obj[cube.id] = { qHyperCubeDef: JSON.parse(JSON.stringify(hyperCubeProps.qHyperCubeDef)) } //deep copy the hyperCubeDef
            obj[cube.id].qHyperCubeDef.qDimensions[0].qDef.qFieldDefs[0] = cube.dimension
            obj[cube.id].qHyperCubeDef.qMeasures[0].qDef.qDef = cube.measure
        })
        return obj
    }    
    
    async _xfrmLayout(layout, kpiName){
        let xfrmConfig = this.kpiConfig?.find((k)=> k.id === kpiName)
        let newLayout = {
            ...layout,
            ...xfrmConfig.additionalData            
        }
        
        newLayout["id"] = xfrmConfig.id;

        // Format Single Value
        for (let i = 0; i < xfrmConfig?.singleValueData?.length; i++) {
            const singleValue = xfrmConfig.singleValueData[i];
            newLayout[singleValue.id] = singleValue.type == "text" ? newLayout[singleValue.id].text : newLayout[singleValue.id].value;
        }

        // Format Hypercube Data
        newLayout['data'] = [];
        for (let i = 0; i < xfrmConfig?.cubeData?.length; i++) {
            const cubeName = xfrmConfig.cubeData[i].id;
            let obj = {
                "series": this._getHypercubeData(layout[cubeName].qHyperCube),
                ...xfrmConfig.cubeData[i].additionalData
                //"dimensionLabel": xfrmConfig.cubeData[i].dimensionLabel,
                //"measureLabel": xfrmConfig.cubeData[i].measureLabel
            };   
            xfrmConfig.cubeData[i].singleValueData?.map((val) => { 
                let id = cubeName + '_' + val.id;
                obj[val.id] = val.type == "text" ? newLayout[id].text : newLayout[id].value;
                delete newLayout[id];
            });         
            newLayout['data'].push(obj);
            delete newLayout[cubeName];
        }
        
        // Format Hypercube Default Data
        for (let i = 0; i < xfrmConfig?.defData?.length; i++) {
            const cubeName = xfrmConfig.defData[i].id;
            newLayout['defData'] = {
                "series": this._getHypercubeData(layout[cubeName].qHyperCube),
            }
            delete newLayout[cubeName];
        }
    
        // Format list Data
        for (let j = 0; j < xfrmConfig?.listData?.length; j++) {
            const listName = xfrmConfig.listData[j].id;

            newLayout[listName] = {
                ...this._getListData(layout[listName].qListObject),
                "label": xfrmConfig.listData[j].label
            }
        }
    
        // Call custom transformation on the results
        newLayout = this?.appConfig[kpiName]?.transform(newLayout) || newLayout
        return newLayout
    
    }

    async _setAppSelections(app, appName){
        let appConfig = this.appConfig[appName]

        // Set Qlik Variables
        for (let index = 0; index < appConfig?.variables?.length; index++) {
            const appVariable = appConfig.variables[index];

             // Set Variables
             let varInfo, varId, variable, variableValue, value 
             try {
                 // console.log("Getting Variable by Name")
                 varInfo = await app.getVariableByName({
                     "qName": appVariable.variable
                 })
                 varId = varInfo.id
         
                 // console.log("Getting Variable by Id")
                 variable = await app.getVariableById({
                 "qId": varId
                 })
         
                 variableValue = await variable.getLayout({}) //to view or 
                 // console.log("current layout:", variableValue)
                 value = variableValue.qText
 
                 if(typeof appVariable.value === "number"){
                     // console.log("Setting Number:", appVariable.value)
                     await variable.setNumValue({"qVal": appVariable.value })
                 } else {
                     // console.log("Setting String:", appVariable.value)
                     await variable.setStringValue({ "qVal": appVariable.value })
                 }   
 
                 // console.log("afterValue?", variableValue)
             }
             catch(e){
                 // If Variable does not exist then create Session Variable
                 // console.log("Creating New Variable")
                 variable = await app.createSessionVariable({
                     "qProp":{
                         "qInfo":{
                             "qType":"GenericVariable"
                         },
                         "qName": appVariable.variable,
                         "qDefinition": appVariable.value
 
                     }
                 })
                 // console.log("New Var:", variable)
             }   
 
             variableValue = await variable.getLayout({}) //to view or 
             // console.log("After both:", variableValue)
         }
        
        // Set Qlik Selection Filters
        for (let index = 0; index < appConfig?.selections?.length; index++) {
            const appSelections = appConfig.selections[index];
            
            // If there is a value to select for the field then select it
            if(appSelections.value) {
                let fieldRef = await app.getField({
                    "qFieldName": appSelections.field,
                    "qStateName": "$"
                })
    
                // Set the selection
                await fieldRef.select({
                    "qMatch": appSelections.value,
                    "qSoftLock": false,
                    "qExcludedValuesMode": 0
                })

            }
        }

        return
    }
}

export default KPIController;
console.log("kpi-controller loaded...")


